import React, { useState, useEffect } from "react";
import axios from "axios";
import { toast } from 'react-toastify';
const api = "http://localhost:4000";
const Backlog = () => {
  const [backlog, setBacklog] = useState([]);

  const tableTitles = ["#", "Title" ];

  const getBacklog = async () => {
    const res = await axios.get(`${api}/backlog`);
    console.log(res.data);
    sortBacklog(res.data)
    // setBacklog(res.data);
    
  };

  const removeFromBacklog = async (id, title) => {
    await axios.delete(`${api}/backlog/${id}`);
    getBacklog();
    toast.success(`${title} Completed!`, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      });
  };

  const sortBacklog = (games) => {
    // console.log(e.nativeEvent.path[0].innerText.toLowerCase())
    // const tableTitle =  e.nativeEvent.path[0].innerText.toLowerCase()

    games.sort((a, b) => a.title.localeCompare(b.title))
    setBacklog(games)
    // const copy = [...backlog]
    // copy.sort((a, b) => a.title.localeCompare(b.title))
    // setBacklog(copy)
  };

  useEffect(() => {
    getBacklog();
    
  }, []);




  return (
    <div>
      {backlog ? (
        <table className='table'>
          <thead>
            <tr>
              {tableTitles.map((title, index) => (
                <th key={title} >{title}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {backlog.map((game, index) => (
              <tr key={game._id}>
                <th scope='row'>{index + 1}</th>
                <td>{game.title}</td>
                {/* <td>{game.system}</td> */}
                <td>
                  <button onClick={() => removeFromBacklog(game._id, game.title)} className='btn btn-dark'>
                    Completed
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        "Search a Game"
      )}
    </div>
  );
};

export default Backlog;
